@import '../../assets/scss/com.scss';
.index_search{
    width: 100%;
    height: 110px;
    background-color: $myBackColor;
    padding: 0px 30px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    .search_box{
        width: 100%;
        height: 72px;
        background: #FFFFFF;
        border: 1px solid #BCBCBC;
        border-radius: 36px;
        padding: 0px 29px;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        overflow: hidden;
        input{
            width: 100%;
            height: 100%;
            border: none;
            padding-left: 20px;
            box-sizing: border-box;
        }
        input::placeholder{
           font-size: 24px;
        }
        div{
            font-size: 24px;
            font-family: Microsoft YaHei;
            font-weight: 400;
            color: #B7B7B7;
            padding-left: 20px;
            box-sizing: border-box;
        }
        span{
            font-size: 30px;
            font-family: Microsoft YaHei;
            font-weight: 400;
            color: #363636;
            display: inline-block;
            padding-right: 30px;
            box-sizing: border-box; 
        }
        img{
            width: 26px;
            height: 32px;
        }
    }
}