@import '../../assets/scss/com.scss';
.frame{
    position: fixed;
    top: 0;
    left: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($color: #000000, $alpha: 0.5);
    .frame_top{
        width: 100%;
        height: 80%;
    }
    .frame_fl{
        width: 100%;
        height: 20%;
        background-color: #fff;
        .fl_select{
            width: 100%;
            height: 60%;
            border-bottom: 2px solid #B7B7B7;
            display: flex;
            align-items: center;
            justify-content: space-around;
            .select_box{
                width: 50px;
                font-size: 20px;
                font-family: Microsoft YaHei;
                font-weight: 400;
                color: #9E9E9E;
                padding-top: 17px;
                box-sizing: border-box;
                text-align: center;
                img{
                    width: 36px;
                    height: 33px;
                }
            }
        }
        .fl_cancel{
            width: 100%;
            height: 40%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 28px;
            font-family: Microsoft YaHei;
            font-weight: 400;
            color: #7E7E7E;
        }
    }
}