@import '../../assets/scss/com.scss';
    .right_img{
        display: flex;
        align-items: center;
        justify-content: center;
        img{
            width: 39px;
            height: 37px;
        }
    }
    .three_two{
        width: 100px;
        display: flex;
        flex-direction: column;
        .two_top{
            display: flex;
            justify-content: flex-end;
            img{
                width: 18px;
                height: 18px;
            }
        }
        .two_cen{
            width: 100%;
            text-align: center;
            font-size: 48px;
            font-family: Microsoft YaHei;
            font-weight: bold;
            color: $myBackColor;
            padding: 6px 0px;
            box-sizing: border-box;
            span{
                font-size: 24px;
                font-family: Microsoft YaHei;
                font-weight: bold;
                color: $myBackColor;
            }
        }
        .two_bottom{
            font-size: 24px;
            font-family: Microsoft YaHei;
            font-weight: 400;
            color: #7E7E7E;
        }
    }
    .three_twotext{
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 36px;
        font-family: Microsoft YaHei;
        font-weight: bold;
        color: $myBackColor;
    }
    .right_one{
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 24px;
        font-family: Microsoft YaHei;
        font-weight: 400;
        color: #7E7E7E;
        .one_text{
            padding-bottom: 14px;
            box-sizing: border-box;
            font-size: 36px;
            font-weight: bold;
        }
    }
    .right_three{
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 36px;
        font-family: Microsoft YaHei;
        font-weight: bold;
        color: #7E7E7E;
    }
    .right_line{
        width: 2px;
        height: 112px;
        background: #B7B7B7;
        margin-left: 30px;
        margin-right: 30px;
    }